const ImageModule = require('docxtemplater-image-module-free')

function exportWord(template, exportRow, outputFilename) {
  JSZipUtils.getBinaryContent(template, (error, content) => {
    if (error) {
      console.error(error, 'error')
      return
    }
    var opts = {}
    opts.centered = false
    opts.getImage = (tagValue, tagName) => {
      return new Promise((resolve, reject) => {
        JSZipUtils.getBinaryContent(tagValue, (error, content) => {
          if (error) {
            return reject(error)
          }
          return resolve(content)
        })
      })
    }
    //
    opts.getSize = (img, tagValue, tagName) => {
      // FOR FIXED SIZE IMAGE :
      return [470, 210] // 
      return new Promise((resolve, reject) => {
        var image = new Image()
        image.src = url
        image.onload = () => {
          resolve([image.width, image.height])
        }
        image.onerror = (e) => {
          console.log('img, tagValue, tagName : ', img, tagValue, tagName)
          reject(e)
        }
      })
    }
    var imageModule = new ImageModule(opts)

    var zip = new PizZip(content)
    var doc = new docxtemplater()
      .loadZip(zip)
      .attachModule(imageModule)
      .compile()
    doc
      .resolveData(exportRow)
      .then((res) => {
        doc.render()
        var out = doc.getZip().generate({
          type: 'blob',
          mimeType:
            'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
        })
        saveAs(out, outputFilename + '.docx')
      })
      .catch((error) => {
        let e = {
          message: error.message,
          name: error.name,
          stack: error.stack,
          properties: error.properties,
        }
        console.log(
          JSON.stringify({
            error: e,
          })
        )
        throw error // 
      })
  })
}

