<template>
    <el-container style="height: 100vh;">
        <Nav :isCollapse="isCollapse" @update:isCollapse="updateIsCollapse" :isSelect="selected"></Nav>

        <el-container>

            <el-main>
                <el-header>


                    <el-button size="small" type="primary" icon="el-icon-plus"
                        @click="dialogFormVisible = true">model</el-button>


                </el-header>
                <el-container>
                    <el-dialog title="Model" :visible.sync="dialogFormVisible">
                        <el-form :model="form">

                            <el-form-item label="MODEL " :label-width="formLabelWidth">
                                <el-select v-model="form.region" placeholder="MODEL">
                                    <el-option label="MODEL1" value="shanghai"></el-option>
                                    <el-option label="MODEL2" value="beijing"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="dialogFormVisible = false"> </el-button>
                            <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
                        </div>
                    </el-dialog>

                </el-container>
                <el-row :gutter="20">
                    <el-col :span="8" v-for="(knowledge, index) in modellist" :key="index">
                        <el-card
                            :style="{ height: '200px', display: 'flex', flexDirection: 'column',}"
                            shadow="hover">
                            <template #header>
                                <div class="headerbutton"
                                    style="display: flex; justify-content: space-between; align-items: center; height: 50px;">
                                    <div>
                                        <i class="el-icon-picture-outline-round" @click="showGraph(index)"></i>
                                        <span>{{ knowledge }}</span>
                                    </div>
                                    <el-button size="mini" type="success" icon="el-icon-check" circle
                                        v-if="index == 1"></el-button>
                                </div>
                            </template>
                            <div class="text item"
                                >
                                {{ knowledge }} {{ knowledge }} {{ knowledge }}
                            </div>
                            <el-row style="margin-top: 20px;">
                                <el-col :span="8" style="text-align: center;">
                                    <i class="el-icon-edit" @click="showConfigDialog(index)"></i>
                                </el-col>
                                <el-col :span="8" style="text-align: center;">
                                    <i class="el-icon-picture-outline-round"
                                        @click="openDialogAndRenderGraph(index)"></i>
                                </el-col>
                                <el-col :span="8" style="text-align: center;">
                                    <i class="el-icon-delete" @click="deleteKnowledge(index)"></i>
                                </el-col>
                            </el-row>
                        </el-card>
                    </el-col>
                </el-row>

                <el-dialog title="" :visible.sync="settingdialogFormVisible">
                    <el-form :model="settingform">
                        <el-row>
                            <el-col :span="8" v-for="(config, index) in configArray" :key="index">
                                <el-form-item :label="config.label">
                                    <el-select v-model="settingform[config.key]" :placeholder="'' + config.label">
                                        <el-option v-for="(option, optionIndex) in config.options" :key="optionIndex"
                                            :label="option.label" :value="option.value"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>


                    <div slot="footer" class="dialog-footer">
                        <el-button @click="settingdialogFormVisible = false"> </el-button>
                        <el-button type="primary" @click="savesetting"> </el-button>
                    </div>
                </el-dialog>

            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import { getChatMsg, gethistory, getstatic } from "@/api/getData";
import Emoji from "@/components/Emoji.vue";
import Nav from "@/components/Nav.vue";
import commonMethodsMixin from '../../util/publicfun.js';
import StreamText from '@/components/StreamText.vue';
export default {
    mixins: [commonMethodsMixin],
    components: {
        Emoji,
        Nav,
        StreamText
    },
    data() {
        return {

            settingdialogFormVisible: false,
            selected: '7',
            currentDate: new Date(),
            isCollapse: false,
            dialogTableVisible: false,
            dialogFormVisible: false,
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            formLabelWidth: '120px',
            modellist: [],
            configArray: [
                {
                    label: 'tempreature',
                    key: 'config1',
                    options: [
                        { label: '1-1', value: 'option1-1' },
                        { label: '1-2', value: 'option1-2' },
                        // ...
                    ]
                },
                {
                    label: 'top_k',
                    key: 'config2',
                    options: [
                        { label: '2-1', value: 'option2-1' },
                        { label: '2-2', value: 'option2-2' },
                        // ...
                    ]
                },
                {
                    label: 'top_p',
                    key: 'config3',
                    options: [
                        { label: '3-1', value: 'option3-1' },
                        { label: '3-2', value: 'option3-2' },
                        // ...
                    ]
                },
                {
                    label: '',
                    key: 'config4',
                    options: [
                        { label: '4-1', value: 'option4-1' },
                        { label: '4-2', value: 'option4-2' },
                        // ...
                    ]
                },
                {
                    label: '',
                    key: 'config5',
                    options: [
                        { label: '5-1', value: 'option5-1' },
                        { label: '5-2', value: 'option5-2' },
                        // ...
                    ]
                },
                {
                    label: '',
                    key: 'config6',
                    options: [
                        { label: '6-1', value: 'option6-1' },
                        { label: '6-2', value: 'option6-2' },
                        // ...
                    ]
                },
            ],
            settingform: {
                name: '',
                content: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },



        };
    },
    created() {
        getstatic().then((res) => {
            console.log("getstaticres", res)
            this.modellist = res.data.models
        }).catch((err) => {
            console.log("err", err)
        })

    },
    computed: {
        // 
        filterData() {
            return function (data) {
                let obj = [];
                // obj
                this.tableData.filter((item) => {
                    obj.push({
                        text: item[data.value],
                        value: item[data.value],
                    });
                });
                //column
                return this.deWeight(obj);
            };
        },
    },

    methods: {
        savesetting() {
            this.settingdialogFormVisible = false;
            console.log("settingdialogFormVisible", this.settingform)
        },
        showConfigDialog(index) {
            this.settingdialogFormVisible = true
        },
        updateIsCollapse(value) {
            this.isCollapse = value;
            // this.updateIsCollapse(value);
        },
        toggleCollapse() {
            this.isCollapse = !this.isCollapse; // 
        },
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        goToMain() {
            window.location.href = '#/ChatHome';
        },
        goToKnowledgeQA() {
            window.location.href = '#/KnowLedgeChat';
        },
        goToFreeChat() {
            window.location.href = '#/FreeChat';
        },
        goToCheckChat() {
            window.location.href = '#/CheckChat';
        },
        goToTitleSetChat() {
            window.location.href = '#/TitleSetChat';
        },
        goToKnowSetting() {
            window.location.href = '#/KnowSetting';
        },
        goToPrompt() {
            window.location.href = '#/Prompt';
        },
        goToSelectModel() {
            window.location.href = '#/SelectModel';
        },
        goToHelp() {
            window.location.href = '#/HelpChat';
        },
        submitForm() {
            console.log('Submitting form data along with the fileList:',);

        },
        filterHandler(value, row, column) {
            const property = column["property"];
            return row[property] === value;
        },
        filterTag(value, row, column) {
            const property = column["property"];
            return row[property] === value;
        },
        // 
        deWeight(arr) {
            for (var i = 0; i < arr.length - 1; i++) {
                for (var j = i + 1; j < arr.length; j++) {
                    if (arr[i].text == arr[j].text) {
                        arr.splice(j, 1);
                        j--;
                    }
                }
            }
            return arr;
        },
        _filterChange() {
            this.totalSize = this.$refs.table.tableData.length;
            console.log(this.totalSize);
        },

    }
}
</script>

<style>

.headerbutton {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.text.item {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>