<template>
    <el-container style="height: 100vh;">
        <Nav :isCollapse="isCollapse" @update:isCollapse="updateIsCollapse" :isSelect="selected"></Nav>

        <el-container>

            <el-main>

                <el-header>


                    <el-button size="small" type="primary" icon="el-icon-plus"
                        @click="dialogFormVisible = true"></el-button>

                </el-header>
                <el-container style="">


                    <!-- <div> -->
                    <el-row type="flex" style=" width: 100%;flex-wrap: wrap;">
                        <el-col :span="8" v-for="(knowledge, index) in kbs" :key="index">
                            <el-card height="80px" shadow="hover">
                                <div slot="header">
                                    <i class="el-icon-picture-outline-round" @click="showGraph(index)"></i>

                                    <span>{{ knowledge.name }}</span>
                                </div>
                                <div class="text item">
                                    {{ knowledge.description }}
                                </div>

                                <el-row style="margin-top: 20px;">
                                    <el-col :span="8">
                                        <i class="el-icon-edit" @click="showConfigDialog(index)"></i>
                                    </el-col>
                                    <el-col :span="8">

                                        <i class="el-icon-picture-outline-round"
                                            @click="openDialogAndRenderGraph(index)"></i>
                                    </el-col>
                                    <el-col :span="8">
                                        <i class="el-icon-delete" @click="deleteKnowledge(index)"></i>

                                    </el-col>

                                </el-row>

                            </el-card>
                        </el-col>
                    </el-row>
                    <!-- </div> -->

                    <el-dialog title="" :visible.sync="centerDialogVisible" width="600px" center
                        :style="{ height: 'auto' }">
                        <div ref="graphContainer" class="graph-container"></div>
                    </el-dialog>

                </el-container>
            </el-main>


            <!-- <el-button type="text" @click="dialogFormVisible = true"> Dialog</el-button> -->

            <el-dialog title="" :visible.sync="dialogFormVisible">
                <el-form :model="form">
                    <el-form-item label="">
                        <el-select v-model="form.name" placeholder="">
                            <el-option label="" value="shanghai"></el-option>
                            <el-option label="" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.content" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-upload class="upload-demo" action="#" :on-preview="handlePreview" :on-remove="handleRemove"
                            :before-remove="beforeRemove" :on-change="handleChange" :file-list="fileList"
                            :http-request="uploadFile">
                            <el-button size="small" type="primary"></el-button>
                            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button type="primary" @click="addNew"> </el-button>
                </div>
            </el-dialog>

            <el-dialog title="" :visible.sync="settingdialogFormVisible">
                <el-form :model="settingform">
                    <el-row>
                        <el-col :span="8" v-for="(config, index) in configArray" :key="index">
                            <el-form-item :label="config.label">
                                <el-select v-model="settingform[config.key]" :placeholder="'' + config.label">
                                    <el-option v-for="(option, optionIndex) in config.options" :key="optionIndex"
                                        :label="option.label" :value="option.value"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>


                <div slot="footer" class="dialog-footer">
                    <el-button @click="settingdialogFormVisible = false"> </el-button>
                    <el-button type="primary" @click="savesetting"> </el-button>
                </div>
            </el-dialog>


        </el-container>
    </el-container>
</template>

<script>
import { getChatMsg, gethistory, getstatic, upload_kg } from "@/api/getData";
import * as d3 from 'd3';
import Emoji from "@/components/Emoji.vue";
import Nav from "@/components/Nav.vue";
import commonMethodsMixin from '../../util/publicfun.js';
import StreamText from '@/components/StreamText.vue';
export default {
    mixins: [commonMethodsMixin],
    components: {
        Emoji,
        Nav,
        StreamText
    },
    data() {

        return {
            selected: '5',
            centerDialogVisible: false,
            graphData: {
                nodes: [
                    { id: 'A', group: 1 },
                    { id: '', group: 2 },
                    { id: 'B', group: 2 },
                    { id: '', group: 2 },
                    { id: '1', group: 2 },
                    { id: 'B1', group: 2 },
                    { id: '1', group: 2 },

                    // { id: '4', group: 2 },
                    // { id: '5', group: 2 },
                    // { id: '6', group: 2 },
                    // { id: '7', group: 2 },
                    // 
                ],

                links: [
                    { source: 'A', target: '', value: 2, label: "" },
                    { source: 'A', target: 'B', value: 20, label: "" },
                    { source: 'A', target: '', value: 2, label: "" },
                    { source: 'A', target: '1', value: 2, label: "" },
                    { source: 'A', target: 'B1', value: 20, label: "" },
                    { source: 'A', target: '1', value: 2, label: "" },
                    // { source: 'A', target: '5', value: 1 },
                    // { source: 'A', target: '6', value: 1 },
                    // { source: 'A', target: '7', value: 1 },
                    // 
                ],
            },
            configArray: [
                {
                    label: '',
                    key: 'config1',
                    options: [
                        { label: '1-1', value: 'option1-1' },
                        { label: '1-2', value: 'option1-2' },
                        // ...
                    ]
                },
                {
                    label: '',
                    key: 'config2',
                    options: [
                        { label: '2-1', value: 'option2-1' },
                        { label: '2-2', value: 'option2-2' },
                        // ...
                    ]
                },
                // {
                //     label: '',
                //     key: 'config3',
                //     options: [
                //         { label: '3-1', value: 'option3-1' },
                //         { label: '3-2', value: 'option3-2' },
                //         // ...
                //     ]
                // },
                {
                    label: '',
                    key: 'config4',
                    options: [
                        { label: '4-1', value: 'option4-1' },
                        { label: '4-2', value: 'option4-2' },
                        // ...
                    ]
                },
                // {
                //     label: '5',
                //     key: 'config5',
                //     options: [
                //         { label: '5-1', value: 'option5-1' },
                //         { label: '5-2', value: 'option5-2' },
                //         // ...
                //     ]
                // },
                // {
                //     label: '6',
                //     key: 'config6',
                //     options: [
                //         { label: '6-1', value: 'option6-1' },
                //         { label: '6-2', value: 'option6-2' },
                //         // ...
                //     ]
                // },
            ],
            settingform: {
                name: '',
                content: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            settingdialogFormVisible: false,
            currentDate: new Date(),
            isCollapse: false,

            dialogFormVisible: false,
            form: {
                name: '',
                content: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            }
            ,
            active: 1,
            configs: [
                {
                    name: "1",
                    content: ""

                },
                {
                    name: "2",
                    content: "2222"

                },
                {
                    name: "3",
                    content: "333"

                }

            ], // 
            kbs: [],
            selectfile:"",
            fileList:[]

        };
    },
    created() {
        getstatic().then((res) => {
            console.log("getstaticres", res)
            this.kbs = res.data.kbs
        }).catch((err) => {
            console.log("err", err)
        })

    },
    mounted() {
        this.loadFiles();
    },
    computed: {
        // 
        filterData() {
            return function (data) {
                let obj = [];
                // obj
                this.tableData.filter((item) => {
                    obj.push({
                        text: item[data.value],
                        value: item[data.value],
                    });
                });
                //column
                return this.deWeight(obj);
            };
        },
    },

    methods: {
        handlePreview(){

        },
        handleRemove(){

        },
        beforeRemove(){

        },

        parseNetworkData(networkData) {
            const nodes = [];
            const links = [];
            // Helper function to add nodes and avoid duplicates
            function addNode(id, group) {
                if (!nodes.find(node => node.id === id)) {
                    nodes.push({ id, group });
                }
            }

            networkData.forEach(entry => {
                const content = entry.content;
                const parts = content.match(/([^]+)/g);
                if (parts.length === 3) {
                    const [subject, relationship, object] = parts;
                    addNode(subject, 1);
                    addNode(object, 2);
                    links.push({
                        source: subject,
                        target: object,
                        value: 1,
                        label: relationship
                    });
                }
            });

            return { nodes, links };
        },



        updateIsCollapse(value) {
            this.isCollapse = value;
            // this.updateIsCollapse(value);
        },
        openDialogAndRenderGraph(index) {
            this.centerDialogVisible = true;

            this.$nextTick(() => {
                this.showGraph(index);
            });
        },
        showGraph(index) {
            console.log("fileList", this.selectfile)
            // upload_kg(this.selectfile, this.handleChunk);

            this.centerDialogVisible = true;
            const data = this.graphData;
            const width = 500;
            const height = 400;
            d3.select(this.$refs.graphContainer).selectAll('*').remove();

            const svg = d3
                .select(this.$refs.graphContainer)
                .append('svg')
                .attr('width', width)
                .attr('height', height);

            const linkDistance = 150; // 
            const chargeStrength = -500; // 

            // // 
            // svg.append('defs').append('marker')
            //     .attr('id', 'arrowhead')
            //     .attr('viewBox', '-0 -5 10 10')
            //     .attr('refX', 25) // 
            //     .attr('refY', 0)
            //     .attr('orient', 'auto')
            //     .attr('markerWidth', 6) // 
            //     .attr('markerHeight', 6) // 
            //     .attr('xoverflow', 'visible')
            //     .append('svg:path')
            //     .attr('d', 'M 0,-5 L 10 ,0 L 0,5')
            //     .attr('fill', '#999')
            //     .style('stroke', 'none');

            const simulation = d3
                .forceSimulation(data.nodes)
                .force('link', d3.forceLink(data.links).id(d => d.id).distance(linkDistance))
                .force('charge', d3.forceManyBody().strength(chargeStrength))
                .force('center', d3.forceCenter(200, height / 2));

            const extendLength = 200;

            // 
            const link = svg.append('g')
                .attr('stroke', '#999')
                .attr('stroke-opacity', 0.6)
                .selectAll('line')
                .data(data.links)
                .enter()
                .append('line')
                .attr('stroke-width', d => Math.sqrt(d.value))
            // .attr('marker-end', 'url(#arrowhead)'); // 

            // 
            const linkText = svg.append('g')
                .selectAll('text')
                .data(data.links)
                .enter()
                .append('text')
                .text(d => d.label) // 
                .attr('font-size', 12)
                .attr('fill', '#000');

            // 
            const initialRadius = 20;
            // 
            const enlargedRadius = 30;

            // 
            const node = svg
                .append('g')
                .selectAll('circle')
                .data(data.nodes)
                .enter()
                .append('circle')
                .attr('r', initialRadius)
                .attr('fill', d => (d.group === 1 ? 'red' : 'green'))
                .call(
                    d3
                        .drag()
                        .on('start', dragstarted)
                        .on('drag', dragged)
                        .on('end', dragended)
                )
                .on('mouseover', function (event, d) {
                    // 
                    d3.select(this).attr('r', enlargedRadius);
                })
                .on('mouseout', function (event, d) {
                    // 
                    d3.select(this).attr('r', initialRadius);
                });

            const text = svg
                .append('g')
                .selectAll('text')
                .data(data.nodes)
                .enter()
                .append('text')
                .text(d => d.id)
                .attr('x', 15)
                .attr('y', 3);

            simulation.on('tick', () => {
                link
                    .attr('x1', d => d.source.x)
                    .attr('y1', d => d.source.y)
                    .attr('x2', d => d.target.x)
                    .attr('y2', d => d.target.y);

                linkText
                    .attr('x', d => (d.source.x + d.target.x) / 2) // 
                    .attr('y', d => (d.source.y + d.target.y) / 2);

                node.attr('cx', d => d.x).attr('cy', d => d.y);

                text.attr('x', d => d.x).attr('y', d => d.y);
            });

            function dragstarted(event, d) {
                if (!event.active) simulation.alphaTarget(0.3).restart();
                d.fx = d.x;
                d.fy = d.y;
            }

            function dragged(event, d) {
                d.fx = event.x;
                d.fy = event.y;
            }

            function dragended(event, d) {
                if (!event.active) simulation.alphaTarget(0);
                d.fx = null;
                d.fy = null;
            }
        },
        handleChunk(chunkValue) {
            try {
                const data = JSON.parse(chunkValue);
                console.log("datadatadata", data)
                this.tableData.unshift(data);
                this.totalItems = this.tableData.length;
            } catch (error) {
                console.error('Error parsing chunk value', error);
            }
        },

        deleteKnowledge() {

        },
        savesetting() {
            this.settingdialogFormVisible = false;
            console.log("settingdialogFormVisible", this.settingform)
        },
        showConfigDialog(index) {
            this.settingdialogFormVisible = true
        },
        addNew() {
            this.configs.push({
                name: this.form.name,
                content: this.form.content
            });
            this.dialogFormVisible = false;
            this.form.name = '';
            this.form.content = '';
            console.log("this.configs", this.configs)
        },
        toggleCollapse() {
            this.isCollapse = !this.isCollapse; // 
        },
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        goToMain() {
            window.location.href = '#/ChatHome';
        },
        goToKnowledgeQA() {
            window.location.href = '#/KnowLedgeChat';
        },
        goToFreeChat() {
            window.location.href = '#/FreeChat';
        },
        goToCheckChat() {
            window.location.href = '#/CheckChat';
        },
        goToTitleSetChat() {
            window.location.href = '#/TitleSetChat';
        },
        goToKnowSetting() {
            window.location.href = '#/KnowSetting';
        },
        goToPrompt() {
            window.location.href = '#/Prompt';
        },
        goToSelectModel() {
            window.location.href = '#/SelectModel';
        },
        goToHelp() {
            window.location.href = '#/HelpChat';
        },
        submitForm() {
            console.log('Submitting form data along with the fileList:',);

        },
        next() {
            if (this.active++ > 2) this.active = 0;
        },
        handleChange(file, fileList) {
            this.fileList = fileList;
            this.saveFiles();
        },
        uploadFile(param) {
            this.selectfile = param
            // const reader = new FileReader();
            // reader.onload = (e) => {
            //     const newFile = {
            //         uid: param.file.uid,
            //         name: param.file.name,
            //         url: e.target.result,
            //         description: '' // 
            //     };
            //     this.fileList.push(newFile);
            //     this.saveFiles();
            // };
            // reader.readAsDataURL(param.file);
        },
        saveFiles() {
            localStorage.setItem('fileList', JSON.stringify(this.fileList));
        },
        loadFiles() {
            const files = localStorage.getItem('fileList');
            if (files) {
                this.fileList = JSON.parse(files);
            }
        },
        editFile(fileUid) {
            const file = this.fileList.find(file => file.uid === fileUid);
            if (file) {
                const newDescription = prompt('', file.description || '');
                if (newDescription !== null) {
                    file.description = newDescription;
                    this.saveFiles();
                }
            }
        },
        deleteFile(fileUid) {
            this.fileList = this.fileList.filter(file => file.uid !== fileUid);
            this.saveFiles();
        }



    }
}
</script>

<style>
.response-options {
    text-align: center;
    padding-left: 20;
    margin-bottom: 20px;
}

.graph-container {
    max-height: 600px;
    overflow-y: auto;
    border: 1px solid #ddd;
    width: 100%;
    /*  */
}

/*  */
.graph-container::-webkit-scrollbar {
    width: 12px;
    /*  */
}

.graph-container::-webkit-scrollbar-track {
    background: #f1f1f1;
    /*  */
    border-radius: 10px;
    /*  */
}

.graph-container::-webkit-scrollbar-thumb {
    background: #888;
    /*  */
    border-radius: 10px;
    /*  */
